<template>
    <el-dialog width="90%"
               top="20px"
               :visible.sync="state.visible">
        <el-tabs v-model="current.type">
            <el-tab-pane label="可视化成果" name="screen">
                <div class="achievement-container">
                    <template v-if="screen.list && screen.list.length">
                        <template v-for="(item, i) in screen.list">
                            <div class="card"
                                 :class="current.type === 'screen' && current.screenIndex === i ? 'active' : ''"
                                 :key="i" @click="select(item, i)">
                                <template v-if="item.cover">
                                    <div class="img-cover"></div>
                                </template>
                                <template v-else>
                                    <img :src="item.cover" class="img-cover">
                                </template>
                                <div class="title-bar">
                                    <el-row>
                                        <el-col :span="18" class="name">{{item.name || '未命名'}}</el-col>
                                        <el-col :span="6" class="state">
                                            <el-button size="mini" type="primary" plain @click="previewScreen(item.id)">
                                                预览
                                            </el-button>
                                        </el-col>
                                    </el-row>
                                </div>
                            </div>
                        </template>
                    </template>
                </div>
            </el-tab-pane>
            <el-tab-pane label="系统成果" name="integration">
                <div class="achievement-container">
                    <template v-if="integration.list && integration.list.length">
                        <template v-for="(item, i) in integration.list">
                            <div class="card"
                                 :class="current.type === 'integration' && current.integrationIndex === i ? 'active' : ''"
                                 :key="i" @click="select(item, i)">
                                <template v-if="item.cover">
                                    <div class="img-cover"></div>
                                </template>
                                <template v-else>
                                    <img :src="item.cover" class="img-cover">
                                </template>
                                <div class="title-bar">
                                    <el-row>
                                        <el-col :span="18" class="name">{{item.name || '未命名'}}</el-col>
                                        <el-col :span="6" class="state">
                                            <el-button size="mini" type="primary" plain @click="previewScreen(item.id)">
                                                预览
                                            </el-button>
                                        </el-col>
                                    </el-row>
                                </div>
                            </div>
                        </template>
                    </template>
                </div>
            </el-tab-pane>
        </el-tabs>
        <span slot="footer" class="dialog-footer">
            <el-button @click="state.visible = false">取 消</el-button>
            <el-button type="primary" @click="loadAchievement">确 定</el-button>
        </span>
    </el-dialog>
</template>

<script>
    import API from '../../config/api'
    import CONST from '../../config/const'
    import _ from 'lodash'

    export default {
        name: "Achievement",
        props: ['config', 'state'],
        data() {
            return {
                current: {
                    type: 'screen',
                    screenIndex: null,
                    integrationIndex: null,
                },
                screen: {
                    list: []
                },
                integration: {
                    list: []
                },
            }
        },
        mounted() {
            this.initAchievement()
        },
        methods: {
            /**
             * 初始化数据
             */
            initAchievement() {
                this.initScreen()
                this.initIntegration()
            },
            /**
             * 初始化可视化成果
             */
            initScreen() {
                this.ajax({
                    url: API.SCREEN.LIST,
                    method: CONST.AJAX.POST,
                    data: {}
                }).then(response => {
                    this.screen.list = response.data
                })
            },
            /**
             * 初始化系统成果
             */
            initIntegration() {
                this.ajax({
                    url: API.INTEGRATION.LIST,
                    method: CONST.AJAX.POST,
                    data: {}
                }).then(response => {
                    this.screen.list = response.data
                })
            },
            /**
             * 预览
             */
            previewScreen(id) {
                let link = this.$router.resolve({
                    path: './presentation',
                    query: {id: id}
                })
                window.open(link.href, '_blank')
            },
            /**
             * 确定成果
             */
            select(item, index) {
                if (this.current.type === 'screen') {
                    this.current.screenIndex = index
                    this.current.integrationIndex = null
                    this.state.url = 
                        window.location.href.replace(this.$route.path, '/presentation') + '?id=' + item.id;
                } else {
                    this.current.screenIndex = null
                    this.current.integrationIndex = index
                    this.state.url = 
                        window.location.href.replace(this.$route.path, '/integration_presentation') + '?id=' + item.id;
                }
            },
            /**
             * 选择成果
             */
            loadAchievement() {
                this.state.visible = false
                this.state.item.url = this.state.url
                this.state.url = null
            }
        }
    }
</script>

